/* WHITE BACKGROUND CARD */
.card {
  box-shadow: 0 2px 8px rgba(126, 126, 126, 0.25);
  border-radius: 25px;
  height: 40%;
  min-width: 30%;
  display: flex;
  margin: 0 10px;
}

/* PURPLE BACKGROUND CARD */
.purple-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-image: var(--primaryGradient);
  height: 100%;
  width: 50%;
  border-radius: 25px;
}

.circle {
  height: 150px;
  width: 150px;
  border-radius: 75px;
  background-image: var(--circleGradient);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.title {
  font-size: 1rem;
  color: hsl(var(--white), 0.8);
  font-weight: 700;
  margin-top: 5%;
}

.title-right {
  font-size: 1rem;
  color: rgb(46, 50, 65);
  font-weight: 700;
  margin-top: 5%;
  justify-self: flex-end;
}

.score-big {
  font-size: 3rem;
  color: hsl(var(--white));
}

.score-small {
  color: hsl(var(--white), 0.6);
  font-size: 0.8rem;
}

.great {
  color: hsl(var(--white), 0.9);
  font-size: 1.8rem;
  font-weight: 500;
}

.paragraph-container {
  max-width: 65%;
  margin-bottom: 14%;
}

.paragraph {
  text-align: center;
  color: hsl(var(--white), 0.6);
  font-size: 13px;
}

/* RIGHT SIDE */
.scores-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 50%;
}

.box-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 100%;
}

.box {
  background-color: rgb(214, 214, 214);
  width: 100%;
  height: 30%;
  margin-top: 8px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.scores {
  font-size: 0.7rem;
}

.icon-container {
  display: flex;
}

.category,
.scoreBold {
  font-size: 0.7rem;
  font-weight: 700;
}

.memory {
  background-color: hsl(var(--yellow), 0.2);
}

.reaction {
  background-color: hsl(var(--red), 0.2);
  margin-top: 2.5rem;
}

.verbal {
  background-color: hsl(var(--green), 0.2);
}

.visual {
  background-color: hsl(var(--blue), 0.2);
}

.categoryMemory {
  color: hsl(var(--yellow));
}

.categoryReaction {
  color: hsla(var(--red));
}

.categoryVerbal {
  color: hsl(var(--green));
}

.categoryVisual {
  color: hsl(var(--blue));
}

button {
  margin-bottom: 1rem;
  margin-top: 4rem;
  width: 75%;
  padding: 0.8rem 0;
  background: rgb(47, 57, 102);
  color: aliceblue;
  border-radius: 20px;
  border: none;
}

button:hover {
  background: var(--primaryGradient);
  cursor: pointer;
}

@media (max-width: 375px) {
  .card {
    flex-direction: column;
    box-shadow: none;
    border-radius: 0;
    margin: 0;
    width: 100%;
    height: 100%;
    justify-content: flex-start;
  }

  .purple-card {
    border-radius: 0 0 25px 25px;
    width: 100%;
    max-height: 41%;
  }

  .circle {
    min-height: 150px;
    min-width: 150px;
  }
  .scores-container {
    width: 100%;
    height: 60%;
    align-items: center;
  }
  .title-right {
    align-self: flex-start;
    margin-bottom: 1rem;
  }
  button {
    width: 100%;
    border-radius: 100rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  .reaction{
    margin-top: 1rem;
  }
}
