*{
  font-family: 'Hanken Grotesk', sans-serif;
  box-sizing: border-box;
  margin: 0;
}

.page{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height:100vh;
}


:root{
    --circleGradient: linear-gradient(to top, hsla(241, 72%, 46%, 0),  hsla(256, 72%, 46%, 1));
    --primaryGradient: linear-gradient(to top, hsl(241, 81%, 54%), hsl(252, 100%, 67%));
    --red: 0, 100%, 67%;
    --yellow: 39, 100%, 56%;
    --green: 166, 100%, 37%;
    --blue: 234, 85%, 45%;
    --white: 0,0%,100%;
}


@media(max-width:375px){
  .page{
    flex-direction: column;
    justify-content: flex-start;
  }
}